.App {
  text-align: center;
  max-width: 90%;
  margin: 0 auto;
}

.controls {
  width: 100%;
  margin-top: 30%;
  height: 200px;
}

.controls__control {
  width: 50%;
  padding: 20px;
  box-sizing: border-box;
  float: left;
  cursor: pointer;
  box-shadow: 0 6px 3px rgba(0,0,0,.16)
}

.controls__control:active, .controls__control:hover {
  box-shadow: none;
}

.controls__control img {
  max-width: 32px;
}

.controls__minus {
  background:#cc3535;
  border-radius: 10px 0 0 10px;
}

.controls__plus {
  background:#4eb54e;
  border-radius: 0 10px 10px 0;
}

.status {
  font-family: 'Helvetica Neue', 'Helvetica', sans-serif;
  font-size: 3em;
}

.status__peopleValue {
  font-weight: bold;;
}





